import React, { useState, useEffect } from 'react';
import { database } from './firebaseConfig'; // Firebase database instance
import {
  Container,
  Typography,
  TextField,
  Button,
  List,
  ListItem,
  ListItemText,
  ListItemSecondaryAction,
  IconButton,
} from '@mui/material'; // Material-UI components
import { ref, onValue, push, set, remove } from 'firebase/database';
import { Delete, Edit, VolumeUp } from '@mui/icons-material';

const App: React.FC = () => {
  const [inputText, setInputText] = useState<string>('');
  const [sentences, setSentences] = useState<string[]>([]);

  useEffect(() => {
    // Fetch sentences from Firebase database
    const sentencesRef = ref(database, 'sentences');
    onValue(sentencesRef, (snapshot) => {
      const sentencesData = snapshot.val();
      if (sentencesData) {
        const sentencesArray = Object.values(sentencesData);
        setSentences(sentencesArray as string[]);
      } else {
        setSentences([]);
      }
    });
  }, []); // useEffect dependency array is empty for initial run only

  const handleInputChange = (e: React.ChangeEvent<HTMLInputElement>) => {
    setInputText(e.target.value);
  };

  const handleSubmit = () => {
    if (inputText.trim() !== '') {
      const newSentenceRef = push(ref(database, 'sentences'));
      set(newSentenceRef, inputText).then(() => {
        setInputText(''); // Clear input field
        // Update state with newly added sentence
        setSentences([...sentences, inputText]);
      }).catch((error) => {
        console.error('Error adding document: ', error);
      });
    }
  };

  const handleSpeak = (text: string) => {
    if ('speechSynthesis' in window) {
      const utterance = new SpeechSynthesisUtterance(text);
      utterance.lang = 'en-US'; // Set language to English (United States)
      window.speechSynthesis.speak(utterance);
    } else {
      alert('This browser does not support speech synthesis.');
    }
  };

  const handleDelete = (sentence: string) => {
    const sentencesRef = ref(database, 'sentences');
    onValue(sentencesRef, (snapshot) => {
      snapshot.forEach((childSnapshot) => {
        const val = childSnapshot.val();
        if (val === sentence) {
          const key = childSnapshot.key;
          if (key) {
            remove(ref(database, `sentences/${key}`)).then(() => {
              // Update state to remove the deleted sentence
              const updatedSentences = sentences.filter(item => item !== sentence);
              setSentences(updatedSentences);
            }).catch((error) => {
              console.error('Error deleting document: ', error);
            });
          }
        }
      });
    });
  };

  const handleEdit = (sentence: string) => {
    setInputText(sentence);
  };

  return (
    <Container maxWidth="md">
      <Typography variant="h1" align="center" gutterBottom>
        Text to Speech with Firebase
      </Typography>
      <TextField
        label="Enter text"
        variant="outlined"
        fullWidth
        value={inputText}
        onChange={handleInputChange}
        style={{ marginBottom: '10px' }}
      />
      <Button
        variant="contained"
        color="primary"
        fullWidth
        onClick={handleSubmit}
        style={{ marginBottom: '20px' }}
      >
        Save
      </Button>
      <Typography variant="h2" gutterBottom>
        Saved Sentences:
      </Typography>
      <List>
        {sentences.map((sentence, index) => (
          <ListItem key={index} disablePadding>
            <ListItemText primary={sentence} />
            <ListItemSecondaryAction>
              <IconButton onClick={() => handleEdit(sentence)}>
                <Edit />
              </IconButton>
              <IconButton onClick={() => handleDelete(sentence)}>
                <Delete />
              </IconButton>
              <IconButton onClick={() => handleSpeak(sentence)}>
                <VolumeUp />
              </IconButton>
            </ListItemSecondaryAction>
          </ListItem>
        ))}
      </List>
    </Container>
  );
};

export default App;
